@import url(//db.onlinewebfonts.com/c/49406bd70f319809254b2661e03dfb76?family=MarkW01-Heavy);
* {
  padding: 0;
  margin: 0;
  outline: none;
  border: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
    'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100vw;
  min-height: 100vh;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

@font-face {
  font-family: 'FF Mark';
  src: local('FF Mark Regular'), local('FF-Mark-Regular'), url(/honor-watch/static/media/Mark-Regular.968a3cb9.woff2) format('woff2'),
    url(/honor-watch/static/media/Mark-Regular.4cdc95f1.woff) format('woff'), url(/honor-watch/static/media/Mark-Regular.2662258e.ttf) format('truetype');
  font-weight: 400;
  font-style: normal;
}

.Header {
  position: relative;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.Header__logo {
  cursor: pointer;
  width: 30px;
  height: 20px;
}

.Header__brandName {
  cursor: pointer;
  width: 178px;
  height: 35px;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}

.Header__nav {
  flex-basis: 33%;
  margin-right: 50px;
}

.Header__list {
  list-style: none;
  display: flex;
  justify-content: space-between;
  align-items: center;

  font-family: 'FF Mark';
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 25px;

  color: #000000;
}

.Header__link {
  cursor: pointer;
  color: #000000;
  text-decoration: none;
}

.Header__button {
  width: 100px;
  background-color: #f29494;
  color: #ffffff;
  padding: 0.8rem;
  cursor: pointer;
}

.Header__button__link {
  text-decoration: none;
  color: #ffffff;
}

.Card {
  cursor: pointer;
  margin: 10px;
  display: flex;
  width: 164px;
  height: 212px;
  align-items: center;
  flex-direction: column;
  background: #ffffff;
  box-shadow: 0px 0px 50px rgba(0, 0, 0, 0.1);
  border-radius: 31px;
}

.Card__title {
  padding-left: 10px;
  padding-top: 10px;
  font-style: normal;
  font-weight: 800;
  font-size: 21px;
  color: #000000;
}

.Card__text {
  margin-top: -10px;
  margin-left: 40px;
  font-style: normal;
  font-weight: 275;
  font-size: 14px;
  line-height: 25px;

  color: #868686;
}

.Card__img {
  margin-left: 22px;
  width: 143px;
}

.Main {
  display: flex;
}

.Main__left {
  position: relative;
  padding: 80px 60px;
}

.Main__left__title {
  font-style: normal;
  font-weight: 800;
  font-size: 60px;
  /* identical to box height */

  color: #000000;
}

.Main__left__text {
  font-family: 'FF Mark';
  font-style: normal;
  font-weight: normal;
  font-size: 22px;
  line-height: 70px;

  letter-spacing: 0.5em;
  text-transform: uppercase;

  color: #ef9c9c;
}

.Main__left__secondText {
  max-width: 400px;
  font-family: 'FF Mark';
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 25px;
  text-align: justify;

  color: #868686;
}

.Main__card {
  position: absolute;
  display: flex;
  flex-direction: row-reverse;
  bottom: 0;
  right: 15%;
}

.Main__right {
  position: relative;
}

.Main__right__img {
  cursor: pointer;
  padding: 0 20px;
  width: 600px;
  max-width: 100%;
  height: auto;
}

.Main__right__arrow {
  cursor: pointer;
  position: absolute;
  right: -85px;
  top: 50%;
  transform: translateY(-50%);
}


.Navbar {
  padding: 90px 50px;
  position: fixed;
  transform: translateX(-100%);
  left: 0;
  top: 0;
  width: 21%;
  height: 100vh;
  background-color: #000000;
}

.Navbar__close {
  cursor: pointer;
  position: absolute;
  top: 20px;
  right: 25px;
}

.Navbar__close__icon {
  width: 18px;
}

.Navbar__brandName {
  position: absolute;
  width: 178px;
  height: 35px;

  bottom: 25px;
  right: 0px;
  fill: #ffffff;
}

.Navbar__brandName path {
  fill: #ffffff;
}

.Navbar__title {
  font-family: 'FF Mark';
  font-style: normal;
  font-weight: normal;
  font-size: 27px;
  line-height: 61px;
  color: #ffffff;
}

.Navbar__link {
  color: #ffffff;
  text-decoration: none;
}

.Navbar__smartPhone::after {
  content: '';
  display: block;
  width: 0px;
  background-color: #fff;
  height: 2px;
  transition: width 0.4s ease;
}

.Navbar__home::after {
  content: '';
  display: block;
  width: 0px;
  background-color: #fff;
  height: 2px;
  transition: width 0.4s ease;
}

.Navbar__store::after {
  content: '';
  display: block;
  width: 0px;
  background-color: #fff;
  height: 2px;
  transition: width 0.4s ease;
}

.Navbar__store.added::after {
  width: 70%;
}

.Navbar__home.added::after {
  width: 70%;
}

.Navbar__smartPhone.added::after {
  width: 70%;
}

.Navbar__switch {
  margin-top: 20px;
}

.Navbar__switch__label {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

/* Hide default HTML checkbox */
.Navbar__switch__input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.Navbar__switch__slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  transition: 0.4s;
}

.Navbar__switch__slider:before {
  position: absolute;
  content: '';
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  transition: 0.4s;
}

.Navbar__switch__input:checked + .Navbar__switch__slider {
  background-color: #2196f3;
}

.Navbar__switch__input:focus + .Navbar__switch__slider {
  box-shadow: 0 0 1px #2196f3;
}

.Navbar__switch__input:checked + .Navbar__switch__slider:before {
  transform: translateX(26px);
}

/* Rounded sliders */
.Navbar__switch__slider.round {
  border-radius: 34px;
}

.Navbar__switch__slider.round:before {
  border-radius: 50%;
}

.App {
  width: 1200px;
  max-width: 100%;
  padding: 30px;
  margin: 0 auto;
}

